html,
body,
.main {
  position: relative;
}

body {
  min-width: 980px;
  color: #6e6e6e;
  background: #fff;
  font-size: 14px;
  line-height: 1.4;
}

.main {
  margin: 0 auto;
  max-width: 1200px;
}

.title-lg {
  color: #2e2e2e;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.2;
}

.text-uppercase {
  text-transform: uppercase;
}

.color-red {
  color: #ec4a41;
}

.color-green {
  color: #22B745;
}

.main {
  max-width: 1200px;
}

.main__document {
  padding: 0 20px;
}

.main__body {
  overflow: auto;
  padding: 40px 20px;
  border: 1px solid #eee;
  max-height: calc(100vh - 240px);
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 10px;
  margin: 0;
  border: none;
  min-width: 40px;
  height: 40px;
  color: #2e2e2e;
  background: #eee;
  font-size: 16px;
  font-weight: 300;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  transition: background 0.175s ease, box-shadow 0.175s ease, opacity 0.175s ease;
  cursor: pointer;
  user-select: none;
}

.btn:active {
  color: #2e2e2e;
  background: #dadada;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn_block {
  width: 100%;
}

.btn_lg {
  padding: 0 15px;
  min-width: 60px;
  height: 60px;
  font-size: 19px;
  line-height: 60px;
}

.btn_green {
  color: #fff;
  background: #22B745;
}

.btn_green:active {
  color: #fff;
  background: #22B745;
}

.btn_red {
  color: #fff;
  background: #ec4a41;
}

.btn_red:active {
  color: #fff;
  background: #ec4a41;
}

.btn_blue {
  color: #fff;
  background: #2e99db;
}

.btn_blue:active {
  color: #fff;
  background: #2e99db;
}

.btn_darkgray {
  color: #fff;
  background: #2e2e2e;
}

.btn_darkgray:active {
  color: #fff;
  background: #2e2e2e;
}

.btn_primary {
  color: #fff;
  background: #2e99db;
}

.btn_primary:active {
  color: #fff;
  background: #2e99db;
}

.btn:disabled, .btn.is-disabled {
  opacity: 0.5;
  pointer-events: none;
}

button.btn {
  line-height: normal;
}

.btn-delete {
  display: inline-block;
  vertical-align: top;
  width: 60px;
  height: 60px;
  background: none;
  font-size: 0;
  line-height: 60px;
}

.btn-delete:active svg {
  fill: #cc1e14;
}

.btn-delete svg {
  display: inline-block;
  vertical-align: middle;
  fill: #ec4a41;
  font-size: 18px;
}

.input {
  padding: 0 12px;
  border: 1px solid #cccccc;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:not([readonly]):focus {
  border-color: #2e99db;
}

.input:disabled, .input[readonly] {
  border-color: #aeaeae;
  background: #ededed;
  cursor: not-allowed;
}

.input.is-error {
  border-color: #ec4a41;
}

.expiration-date {
  display: flex;
  margin: -10px;
}

.expiration-date__col {
  flex: 0 0 120px;
  padding: 10px;
}

.select {
  padding: 0 30px 0 12px;
  border: 1px solid #cccccc;
  border-radius: 0;
  width: 100%;
  height: 40px;
  background: url(../img/angle-down.svg) no-repeat #fff;
  background-position: right 12px center;
  background-size: auto 6px;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.select::-ms-expand {
  display: none;
}

.select:not([readonly]):focus {
  border-color: #2e99db;
}

.select:disabled, .select[readonly] {
  border-color: #aeaeae;
  background: #ededed;
  cursor: not-allowed;
}

.select.is-error {
  border-color: #ec4a41;
}

.checkbox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 30px;
  min-height: 30px;
  user-select: none;
}

.checkbox_middle .checkbox__text {
  padding-top: 5px;
  padding-bottom: 5px;
}

.checkbox input {
  display: none;
}

.checkbox input:checked ~ .checkbox__icon .icon {
  opacity: 1;
}

.checkbox input:disabled ~ .checkbox__icon, .checkbox input:disabled ~ .checkbox__text {
  opacity: 0.5;
  cursor: not-allowed;
}

.checkbox__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #ccc;
  border-radius: 3px;
  width: 30px;
  height: 30px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.25s;
}

.checkbox__icon .icon {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
  fill: #2e2e2e;
  font-size: 14px;
  transform: translate3d(-50%, -50%, 0);
  transition: opacity 0.25s, background 0.25s;
}

.checkbox__text {
  display: inline-block;
  vertical-align: top;
  padding: 5px 0 5px 40px;
  cursor: pointer;
  color: #7b7b7b;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  transition: color 0.25s;
}

.label {
  display: inline-block;
  vertical-align: top;
  color: #616161;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}

label[for] {
  cursor: pointer;
  user-select: none;
}

.steps {
  position: relative;
  padding: 20px;
  color: #c1c1c1;
  font-size: 0;
}

.steps__header {
  position: relative;
}

.steps__header:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 13px;
  margin-bottom: -2px;
  border-bottom: 4px solid #c1c1c1;
  width: 100%;
}

.steps__list {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.steps__item {
  position: relative;
  z-index: 1;
  display: table-cell;
  vertical-align: bottom;
  table-layout: fixed;
  overflow: hidden;
  font-size: 14px;
  text-align: center;
}

.steps__item:last-child .steps__icon-active:after {
  width: 50%;
}

.steps__in {
  display: block;
  color: #c1c1c1;
}

.steps__in:hover .steps__title,
.steps__in:hover .steps__description {
  color: #2e99db;
}

.steps__in[data-step-state*="active"] .steps__title,
.steps__in[data-step-state*="active"] .steps__description {
  color: #2e99db;
}

.steps__in[data-step-state*="active"] .steps__icon-in,
.steps__in[data-step-state*="active"] .steps__icon-active {
  transition-delay: 0.25s;
}

.steps__in[data-step-state*="active"] .steps__icon-in {
  transform: translate3d(calc(-50% + 13px), 0, 0);
}

.steps__in[data-step-state*="active"] .steps__icon-active {
  transform: translate3d(calc(50% - 13px), 0, 0);
}

.steps__in[data-step-state*="done"] .steps__icon-in,
.steps__in[data-step-state*="done"] .steps__icon-active {
  transform: translate3d(0, 0, 0);
}

.steps__title, .steps__description {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition: color 0.225s ease-in-out;
}

.steps__title {
  font-size: 18px;
  font-weight: 500;
  line-height: 24px;
}

.steps__description {
  height: 18px;
  font-size: 16px;
  font-weight: 300;
  line-height: 18px;
}

.steps__item-circle:before, .steps__progress-circle:before {
  content: '';
  display: block;
  margin: 0 auto;
  border-radius: 50%;
  width: 26px;
  height: 26px;
}

.steps__item-circle:before {
  background: #c1c1c1;
}

.steps__progress-circle:before {
  background: #2e99db;
}

.steps__item-circle {
  margin-top: 8px;
}

.steps__progress {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 20px;
  color: #2e99db;
  pointer-events: none;
}

.steps__progress-line, .steps__progress-in {
  transition: transform 0.3s ease-in-out;
}

.steps__progress-line {
  overflow: hidden;
  transform: translateX(-100%);
}

.steps__progress-in {
  position: relative;
  transform: translateX(100%);
}

.steps__progress-in:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 13px;
  margin-bottom: -2px;
  border-bottom: 4px solid #2e99db;
  width: 100%;
}

.steps__progress-row {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.steps__progress-circle {
  display: table-cell;
  vertical-align: top;
}

